﻿
input:invalid:not(:checked) + label:before {
  border-color: darken(red, 5%);
}
.js-inValid{
  box-shadow: inset 0 0 0 3px darken(red, 5%);
  border-color: darken(red, 5%) !important;
}

.js-valid{
  border-color: darken(green, 5%) !important;
  box-shadow: inset 0 0 0 3px darken(green, 5%);
}